.buyer_profile {
  height: 100vh;
  overflow-x: hidden;
  .list {
    display: flex;
    background-color: white;
    justify-content: center;
    list-style: none;
  }
  .sections_profile {
    width: 100%;
    height: calc(100vh - 70px);
    background-color: white;
    position: relative;
    top: 70px;
    scroll-behavior: smooth;
    scroll-snap-type: y mandatory;
    scrollbar-width: none; //for firefox
    &::-webkit-scrollbar {
      display: none;
    }

    > * {
      width: 100vw;
      height: calc(100vh - 70px);
      scroll-snap-align: start;
    }
  }
}
